import { Component, OnInit } from '@angular/core';
import { NgbModal, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';

import { Subscription } from 'rxjs';

import { Player } from '../player';
import { PlayerService } from '../player.service';
import { StateService } from '../state.service';
import { State } from '../state';
import { PlayerDetailComponent } from '../player-detail/player-detail.component';

@Component({
  selector: 'app-players',
  templateUrl: './players.component.html',
  styleUrls: ['./players.component.scss']
})
export class PlayersComponent implements OnInit {
  players: Player[];
  state: State;
  playersUpdateSubscription: Subscription;
  stateUpdateSubscription: Subscription;

  constructor(private playerService: PlayerService,
    private stateService: StateService,
    private modalService: NgbModal) {  }

  ngOnInit() {
    this.getPlayers();

    this.playersUpdateSubscription = this.playerService
      .playersChange.subscribe(players => this.players = players);

    this.stateUpdateSubscription = this.stateService
      .stateChange.subscribe(state => this.state = state);

    let ngbModalOptions: NgbModalOptions = {
      backdrop : 'static',
      keyboard : false
    };
    const modalRef = this.modalService.open(PlayerDetailComponent, ngbModalOptions);
      // modalRef.componentInstance.player = question;
  }

  ngOnDestroy() {
    this.playersUpdateSubscription.unsubscribe();
    this.stateUpdateSubscription.unsubscribe();
  }

  getPlayers(): void {
    this.playerService.getPlayers()
      .subscribe(players => {
        this.players = players;

        if(this.players.length > 0) {
          this.stateService.addState({ id: 0, currentPlayerIndex: 0 } as State).subscribe(
            state => {
              this.state = state;
            }
          );
        }
    });
  }

  add(playerName: string): void {
    playerName = playerName.trim();
    if (!playerName) { return; }
    this.playerService.addPlayer({ playerName , score: 0 } as Player)
      .subscribe(player => {
        this.players.push(player);
      });
  }

  delete(player: Player): void {
    this.players = this.players.filter(h => h !== player);
    this.playerService.deletePlayer(player).subscribe();
  }

}
