import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

import { Question } from './question';

@Injectable({ providedIn: 'root' })
export class QuestionService {

  //TODO1: REPLACE THE URL BELOW WITH THE ONE FOR YOUR QUESTIONS API, FROM PART II, SECTION e, ITEM iii
  private questionsUrl = 'api';

  httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
  };

  constructor(private http: HttpClient) { }

  /** GET questions from the server */
  getQuestions(): Observable<Question[]> {
    return this.http.get<Question[]>(this.questionsUrl)
      .pipe(
        tap(_ => this.log('fetched questions')),
        catchError(this.handleError<Question[]>('getQuestions', []))
      );
  }

  /** GET question by id. Return `undefined` when id not found */
  getQuestion<Data>(category: string, difficulty: number): Observable<Question> {
    const url = `${this.questionsUrl}/${category}/${difficulty}`;
    return this.http.get<Question[]>(url)
      .pipe(
        map(questions => questions[0]), // returns a {0|1} element array
        tap(h => {
          const outcome = h ? `fetched` : `did not find`;
          this.log(`${outcome} question category=${category}, difficulty=${difficulty}`);
        }),
        catchError(this.handleError<Question>(`getQuestion category=${category}, difficulty=${difficulty}`))
      );
  }

  /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      this.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

  /** Log a PlayerService message to the console */
  private log(message: string) {
    console.log(`QuestionService: ${message}`);
  }
}
