import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable, of, Subject, Subscription } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

import { State } from './state';

@Injectable({ providedIn: 'root' })
export class StateService {

  private stateUrl = 'api/state'; 

  stateChange: Subject<State> = new Subject<State>();
  stateUpdateSubscription: Subscription;
  state: State;

  httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
  };

  constructor(
    private http: HttpClient) {
      this.stateUpdateSubscription = this.stateChange
        .subscribe(state => {
          this.state = state;
        });
    }

    /** GET state with hardcoded id 0. Return `undefined` when id not found */
    getState<Data>(): Observable<State> {
      const url = `${this.stateUrl}/?id=0`;
      return this.http.get<State[]>(url)
        .pipe(
          map(states => states[0]), // returns a {0|1} element array
          tap(state => {
            const outcome = state ? `fetched` : `did not find`;
            this.log(`${outcome} state id=0`);
            this.stateChange.next(state);
          }),
          catchError(this.handleError<State>(`getState id=0`))
        );
    }

  //////// Save methods //////////

  /** PUT: add a new state to the server */
  addState (state: State): Observable<State> {
    return this.http.put<State>(this.stateUrl, state, this.httpOptions).pipe(
      tap((newState: State) => {
        this.log(`added state`);
        this.getState().subscribe();
      }),
      catchError(this.handleError<State>('addState'))
    );
  }

  /** PUT: update the state on the server */
  updateState (state: State): Observable<any> {
    return this.http.put(this.stateUrl, state, this.httpOptions).pipe(
      tap(_ => {
        this.log(`updated state`);
        this.getState().subscribe();
      }),
      catchError(this.handleError<any>('updateState'))
    );
  }

  /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error);

      // TODO: better job of transforming error for user consumption
      this.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

  /** Log a StateService message to the console */
  private log(message: string) {
    console.log(`StateService: ${message}`);
  }
}
