import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

import { Player } from '../player';
import { PlayerService } from '../player.service';

@Component({
  selector: 'app-player-detail',
  templateUrl: './player-detail.component.html',
  styleUrls: ['./player-detail.component.scss']
})
export class PlayerDetailComponent implements OnInit {
  playerCreated: boolean;
  playersExist: boolean;
  playerCreateForm: FormGroup;

  constructor(public activeModal: NgbActiveModal,
    private playerService: PlayerService) {
      this.playerCreateForm = new FormGroup({
        playerName: new FormControl(''),
      });
      this.playerCreated = false;
  }

  ngOnInit() {
    this.verifyPlayers();
  }

  verifyPlayers(): void {
    this.playerService.getPlayers()
      .subscribe(players => {
        if(players.length > 0) {
          this.playersExist = true;
        }
    });
  }

  onSubmit(): void {
    var playerName = this.playerCreateForm.value.playerName;
    this.playerService.addPlayer({ playerName } as Player).subscribe(_ => {
      this.playerCreated = true;
      this.playersExist = true;
      this.playerCreateForm.reset();
    });
  }

}
